.about {}

.about-block {
  $this: &;
  position: relative;
  @include cover;
  background-size: auto;
  background-position: 0 100%;
  @include md {
    margin-bottom: 15px;
    background-size: 60%; }
  &:nth-child(1) {
    #{$this}__text {
      top: 10%;
      left: 15%;
      max-width: 595px; }
    #{$this}__img {
      width: 330px;
      // padding-top: 37%
      height: 490px;
      @include r(374) {
        width: 100%; } } }
  &:nth-child(2) {
    margin-top: -20px;
    @include flex(flex-end);
    @include md {
      margin-top: 0; }
    @include sm {
      margin-bottom: 55px; }
    #{$this}__text {
      max-width: 835px;
      top: 50%;
      transform: translate(0, -50%);
      right: 30%;
      @include sm {
        right: auto;
        left: 50%;
        transform: translate(-50%, 0);
        top: auto;
        bottom: -40px;
        width: 90%; } }
    #{$this}__img {
      width: 742px;
      // padding-top: 40%
      height: 520px;
      @include sm {
        width: 100%;
        height: 420px; }
      &[data-loaded="true"] {
        right: -15%;
        position: relative;
        @include md {
          right: 0; } } } }
  &:nth-child(3) {
    @include flex(flex-start, center);
    #{$this}__img {
      width: percentage(5/12);
      border-radius: 0;
      padding-top: 40%;
      background-size: contain;
      // text-align: center
      // img
 }      //   display: inline-block
    #{$this}__qoute-fu {
      width: percentage(7/12); } }
  &:nth-child(4) {
    @include flex(flex-end);
    margin-top: -100px;
    @include xl {
      margin-top: -70px; }
    @include lg {
      margin-top: -50px; }
    @include md {
      margin-top: -30px; }
    @include sm {
      margin-top: 0;
      margin-bottom: 40px; }
    #{$this}__quote {
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      right: 40%;
      @include md {
        right: auto;
        left: 0;
        max-width: 90%; }
      @include sm {
        top: auto;
        left: 50%;
        transform: translate(-50%, 0);
        bottom: -40px;
        width: 90%; } }
    #{$this}__img {
      width: 930px;
      // padding-top: 43%
      height: 610px;
      @include md {
        width: 100%; }
      @include sm {
        height: 400px; }
      &[data-loaded="true"] {
        position: relative;
        right: -10%;
        @include md {
          left: 0; } } } }
  &__img {
    @include cover;
    // pt
    border-radius: 15px;
    position: relative;
    z-index: 1;
    // img
 }    //// border-radius: 15px
  &__img-right {
    position: absolute;
    z-index: 0;
    top: -20px;
    right: 0;
    img[data-loaded="true"] {
      position: relative;
      right: -30%; } }
  &__text {
    position: absolute;
    z-index: 2;
    box-shadow: 0px 3px 57px 0px rgba(0, 0, 0, 0.11);
    background-color: $white;
    border-radius: 15px;
    padding: 45px 15px 40px 50px;
    opacity: 0;
    @include xl {
      padding: 35px 15px 35px 35px; }
    @include lg {
      padding: 30px 15px 30px 30px; }
    @include sm {
      padding: 15px; }
    .title {
      margin-bottom: 20px;
      @include md {
        margin-bottom: 10px; } }
    p {
      @include notlast {
        margin-bottom: 30px;
        @include md {
          margin-bottom: 15px; } } } }
  &__quote {
    max-width: 730px;
    position: relative;
    z-index: 2;
    opacity: 0; }
  &__quote-fu {
    max-width: 770px;
    opacity: 0; } }
